<template>
	<div :class="scaleClass">
		<div class="tip">
			<p>温馨提醒：表单界面右侧有选项卡，必须将基本信息和详情填写完毕才可以提交表单</p>
		</div>
		<el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px" style="text-align: left;">
			<el-tabs  tab-position="right">
			    <el-tab-pane label="基本信息">
			    	<el-form-item label="商品名称" prop="spmc">
						<el-input type="text" v-model="ruleForm.spmc" placeholder="商品名称" clearable maxlength="60" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="采购编码" prop="itemno">
						<el-input type="text" v-model="ruleForm.itemno" placeholder="采购编码" clearable maxlength="40" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="单位" prop="dw">
						<el-select v-model="ruleForm.dw" placeholder="单位" clearable filterable >
						    <el-option
						      v-for="item in ruleForm.dws"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
					    </el-select>
					</el-form-item>
					<el-form-item label="款号" prop="spcode">
						<el-input type="text" v-model="ruleForm.spcode" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="商品类别" prop="lbid" >
						<el-cascader
							filterable
							:show-all-levels="false"
							expand-trigger="hover"
						    :options="ruleForm.lboptions"
						    v-model="ruleForm.lbidArr"
						    @change="handleChangelb">
						</el-cascader>
					</el-form-item>
					<el-form-item label="材质" prop="cz">
						<el-input type="text" v-model="ruleForm.cz" placeholder="材质" clearable maxlength="20" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="价格" prop="price">
						<el-input-number v-model="ruleForm.price" controls-position="right"  :min="1"></el-input-number>
					</el-form-item>
			    </el-tab-pane>
			    <el-tab-pane label="详情">
			   		<el-form-item prop="cm">
						<el-select v-model="ruleForm.cm" placeholder="尺码" clearable filterable>
						    <el-option
						      v-for="item in ruleForm.cms"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
					    </el-select>
					    <el-button type="primary" @click="handleCmYsSl">添加</el-button>
					</el-form-item>
					<el-form-item>
						<el-table
						      :data="ruleForm.tableData"
						      style="width: 100%">
						      <el-table-column
						        prop="cm"
						        label="尺码"
						        >
						      </el-table-column>
						      <el-table-column
						        prop="ys"
						        label="颜色"
						        >
						      </el-table-column>
						      <el-table-column
						        prop="jdsl"
						        label="数量"
						        >
						      </el-table-column>
						      <el-table-column
							      fixed="right"
							      label="操作"
							      width="360">
							      <template slot-scope="scope">
							      	<el-row>
								        <el-button type="success" size="small" icon="el-icon-edit">修改</el-button>
								        <el-button type="danger"  size="small" icon="el-icon-delete">删除</el-button>
							      	</el-row>
							      </template>
							  </el-table-column>
					    </el-table>
					</el-form-item>
			    </el-tab-pane>
			</el-tabs>
			<el-form-item>
				<el-button type="primary" @click.native.prevent="submitForm" :loading="loading">提交</el-button>
				<el-button  @click.native.prevent="gotoParentPage" >返回</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>
<script>
  import MerchandiseTableAdd from '../../../assets/js/Nav1/children/MerchandiseTableAdd.js'
  export default MerchandiseTableAdd
</script>
<style scoped lang="scss">
@import '../../../assets/css/Nav1/children/MerchandiseTableAdd.scss'
</style>


